.App {
  font-family: verdana;
  text-align: center;
}
.form{
  padding:2%;
}
.form-group{
  padding:2%;
}
.box{
  padding: 1%;
}
.score-display{
  display: grid;
  grid-template-columns: auto auto auto;
  border: 1px solid black;
  border-radius: 50px;  
}
.batsman-group,.bowler{
  text-align: left;
  padding: 2%;
  text-transform: capitalize;
}
.batsman{
  padding-left: 2%;
}

.batsman1{
  padding: 1%;
  
}
.score{
  text-align: center;
  border: 1px solid black;
  padding: 2%;
  color:white;
  background-color: #3341ff;
  border-radius: 50px;
}

.batsman-runs{
  font-size: xx-large;
}

.balls-faced{
  padding-left: 2%;
}
.total-score{
  font-size: 5vw;
  font: bolder;
}
